import React, { useState } from 'react';
import { AppstoreOutlined, MailOutlined, SettingOutlined } from '@ant-design/icons';
import { Drawer, MenuProps } from 'antd';
import { Menu } from 'antd';

const items: MenuProps['items'] = [
    {
        label: 'Home',
        key: 'mail',
        icon: <MailOutlined />,
    },
    {
        label: 'Contact Us',
        key: 'alipay',
    },
];

const App: React.FC = () => {
    const [current, setCurrent] = useState('mail');
    const [openMenu, setOpenMenu] = useState(false);

    const onClick: MenuProps['onClick'] = (e) => {
        console.log('click ', e);
        setCurrent(e.key);
    };

    return <div style={{ height: '100vh', backgroundColor: 'blueviolet' }}>

        <Menu style={{ backgroundColor: 'darkgoldenrod', color: 'white', fontSize: 16 }} onClick={onClick} selectedKeys={[current]} mode="horizontal" items={items} />
        <Drawer bodyStyle={{ backgroundColor: 'darkgoldenrod' }} open={openMenu} closable={false} >
            <AppMenu />
        </Drawer>
    </div>
};


function AppMenu() {
    return <Menu style={{ backgroundColor: 'darkgoldenrod', color: 'white', fontSize: 16 }} mode="inline" items={items} />

}

export default App;